/* RESET */

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1.5;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
*:focus {
  outline: none;
}

body {
  font-family: 'Roboto', sans-serif;
  color: #eeeeee;
  background-color: #000000;
  padding: 5px;
  margin: auto;
  width: 660px;
}

.benchmarkEditor__label,
.p__parameters,
.p__componentSelect {
  font-size: 20px;
  margin-top: 15px;
}

.parametersContainer {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.benchmarkArgumentInputsContainer,
.totalRendersContainer {
  width: 50%;
}

input[type='radio'] {
  display: none;
}

input[type='text'],
input[type='number'] {
  width: 100px;
}

input[type='number']::-webkit-inner-spin-button,
input[type='number']::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type='number'] {
  -moz-appearance: textfield;
}

input[type='number'],
input[type='text'] {
  color: #eeeeee;
  background-color: #282a36;
  border: none;
  padding: 4px;
  margin: 4px 0;
}

.componentSelectContainer {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-flex-direction: row;
          flex-direction: row;
  height: 50px;
  box-sizing: border-box;
}

.benchmarkCode {
  position: relative;
}

.hooksComponentSelectContainer {
  width: 100%;
  position: absolute;
  display: grid;
  grid-template-columns: auto 70.6666666667px 3px 70.6666666667px 3px 70.6666666667px 3px 70.6666666667px;
  grid-template-rows: repeat(1, 50px);
  z-index: 1;
}

.radioContainer,
.memoRadioContainer,
.runButton {
  -webkit-transition: background-color 0.2s ease, color 0.2s ease;
  transition: background-color 0.2s ease, color 0.2s ease;
  background-color: #444962;
  font-weight: bold;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color: white;
}

.radioContainer {
  width: 33.333333%;
  height: calc(100%-3px);
  margin-bottom: 3px;
}

.radioContainer label,
.memoRadioContainer label {
  cursor: pointer;
}

.radioContainer:hover,
.radioContainer:focus,
.memoRadioContainer:hover,
.memoRadioContainer:focus,
.runButton:hover,
.runButton:focus {
  background-color: #e9b183;
  color: black;
}

.radioContainer--checked,
.radioContainer--checked:hover,
.memoRadioContainer--checked,
.memoRadioContainer--checked:hover {
  background-color: #fff586;
  color: black;
}

.radioContainer--checked label:hover,
.radioContainer--checked:hover label:hover {
  cursor: auto;
}

.radioContainer:first-of-type {
  margin-right: 3px;
}

.radioContainer:last-of-type {
  margin-left: 3px;
}

.radioContainer label {
  height: 100%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.memoRadioContainer {
  grid-row-start: 1;
}

.memoRadioContainer label {
  height: 100%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

#memoRadioContainer--naive {
  grid-column: 2 / 3;
}

#memoRadioContainer--memo {
  grid-column: 4 / 5;
}

#memoRadioContainer--function {
  grid-column: 6 / 7;
}

#memoRadioContainer--ref {
  grid-column: 8 / 9;
}

.runButton {
  cursor: pointer;
  font-size: 16px;
  margin: 3px 0;
  width: 100%;
  height: 47px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border: none;
}

.h1Title {
  text-align: center;
  font-size: 40px;
}

.p--Error {
  box-sizing: border-box;
  background-color: #402325;
  border: 1px solid #cc3d3d;
  padding: 4px;
  margin: 5px 0 0 0;
}

.Editor,
pre {
  width: 660px;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.Editor textarea {
  outline: 0;
}

/**
 * Dracula Theme originally by Zeno Rocha [@zenorocha]
 * https://draculatheme.com/
 *
 * Ported for PrismJS by Albert Vallverdu [@byverdu]
 */

code[class*='language-'],
pre[class*='language-'],
.Editor {
  color: #f8f8f2;
  background: #282a36;
  font-family: 'Inconsolata', monospace;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.5;
  -moz-tab-size: 4;
  tab-size: 4;
  -webkit-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
}

/* Code blocks */
pre[class*='language-'] {
  padding: 10px;
  margin: 0;
  overflow: auto;
}

:not(pre) > code[class*='language-'],
pre[class*='language-'] {
  background: #282a36;
}

/* Inline code */
:not(pre) > code[class*='language-'] {
  padding: 0.1em;
  white-space: normal;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: #6272a4;
}

.token.punctuation {
  color: #f8f8f2;
}

.namespace {
  opacity: 0.7;
}

.token.property,
.token.tag,
.token.constant,
.token.symbol,
.token.deleted {
  color: #ff79c6;
}

.token.boolean,
.token.number {
  color: #bd93f9;
}

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
  color: #50fa7b;
}

.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string,
.token.variable {
  color: #f8f8f2;
}

.token.atrule,
.token.attr-value,
.token.function,
.token.class-name {
  color: #f1fa8c;
}

.token.keyword {
  color: #8be9fd;
}

.token.regex,
.token.important {
  color: #ffb86c;
}

.token.important,
.token.bold {
  font-weight: bold;
}

.token.italic {
  font-style: italic;
}

.token.entity {
  cursor: help;
}

